@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v26-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/montserrat-v26-cyrillic_latin-500.ttf") format("truetype");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-v26-cyrillic_latin-600.woff2") format("woff2"), url("../fonts/montserrat-v26-cyrillic_latin-600.ttf") format("truetype");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-v26-cyrillic_latin-700.woff2") format("woff2"), url("../fonts/montserrat-v26-cyrillic_latin-700.ttf") format("truetype");
}
/*===== global styles =====*/
body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  margin: 0;
}

.table_scroll_art6 {
  padding: 49px 0 52px;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.03em;
  color: #494949;
}
.table_scroll_art6 *,
.table_scroll_art6 *::before,
.table_scroll_art6 *::after {
  box-sizing: border-box;
}
@media (max-width: 1600px) {
  .table_scroll_art6 {
    padding: 33px 0 36px;
  }
}
@media (max-width: 1300px) {
  .table_scroll_art6 {
    padding: 30px 0 32px;
  }
}
@media (max-width: 991px) {
  .table_scroll_art6 {
    padding: 35px 0 38px;
  }
}
@media (max-width: 767px) {
  .table_scroll_art6 {
    padding: 34px 0 48px;
    font-size: 14px;
  }
}

.table_scroll_art6__container {
  max-width: 1586px;
  margin: 0 auto;
  padding: 0 43px;
  overflow-x: auto;
  --scrollbar-color-thumb: $color_line;
  --scrollbar-color-track: $color_line_light;
  scrollbar-color: #dcdcdc #e7e7f2;
  scrollbar-width: thin;
  /* Modern browsers with `scrollbar-*` support */
  /* Legacy browsers with `::-webkit-scrollbar-*` support */
}
@media (max-width: 991px) {
  .table_scroll_art6__container {
    padding: 0 33px;
  }
}
@media (max-width: 767px) {
  .table_scroll_art6__container {
    padding: 0 15px;
  }
}
@supports (scrollbar-width: auto) {
  .table_scroll_art6__container {
    scrollbar-color: #dcdcdc #e7e7f2;
    scrollbar-width: thin;
  }
}
@supports selector(::-webkit-scrollbar) {
  .table_scroll_art6__container::-webkit-scrollbar-thumb {
    background: #dcdcdc;
  }
  .table_scroll_art6__container::-webkit-scrollbar-track {
    background: #e7e7f2;
  }
  .table_scroll_art6__container::-webkit-scrollbar {
    max-width: 6px;
    max-height: 6px;
  }
}
.table_scroll_art6__container.table_scroll_art6__container__rotate,
.table_scroll_art6__container.table_scroll_art6__container__rotate .table_scroll_art6__wrapper {
  transform: rotateX(180deg);
}
.table_scroll_art6__container.table_scroll_art6__container__rotate .table_scroll_art6__wrapper {
  padding-top: 30px;
}
@media (max-width: 767px) {
  .table_scroll_art6__container.table_scroll_art6__container__rotate .table_scroll_art6__wrapper {
    padding-top: 25px;
  }
}

.table_scroll_art6__table {
  border-spacing: 0;
  padding-bottom: 30px;
}
.table_scroll_art6__table th,
.table_scroll_art6__table td {
  border: none;
  border-collapse: collapse;
  padding: 10px 10px;
  height: 74px;
}
.table_scroll_art6__table th:first-child,
.table_scroll_art6__table td:first-child {
  width: 186px;
  min-width: 186px;
  padding: 10px 17px;
  text-align: left;
}
.table_scroll_art6__table th:not(:first-child),
.table_scroll_art6__table td:not(:first-child) {
  min-width: 77px;
  text-align: center;
}
@media (max-width: 767px) {
  .table_scroll_art6__table {
    padding-bottom: 25px;
  }
  .table_scroll_art6__table th,
  .table_scroll_art6__table td {
    height: 65px;
  }
  .table_scroll_art6__table th:first-child,
  .table_scroll_art6__table td:first-child {
    width: 156px;
    min-width: 156px;
    padding: 10px 14px;
  }
  .table_scroll_art6__table th:not(:first-child),
  .table_scroll_art6__table td:not(:first-child) {
    min-width: 70px;
  }
}

.table_scroll_art6__row_top {
  font-weight: 700;
  color: #fff;
  background-color: #888;
}

.table_scroll_art6__row th,
.table_scroll_art6__row td {
  border-bottom: 1px solid #dcdcdc;
}
.table_scroll_art6__row th:first-child,
.table_scroll_art6__row td:first-child {
  font-weight: 700;
  color: #e8702e;
}

.table_scroll_art6__row_white {
  background-color: #fff;
}

.table_scroll_art6__row_grey {
  background-color: #f9f9f9;
}

.table_scroll_art6__row_info th:first-child {
  font-weight: 500;
}